<!-- Order.vue -->
<template>
  <div class='Order'>
    <!--    -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--  -->
      <el-row>
        <el-col :span="8">
          <el-input placeholder="">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" style="width: 100%" border>
        <el-table-column type="index" label="#">
        </el-table-column>
        <el-table-column prop="order_number" label="">
        </el-table-column>
        <el-table-column prop="order_price" label="" width="80px">
        </el-table-column>
        <el-table-column prop="pay_status" label="" width="80px">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.pay_status === '0'" type="danger">
            </el-tag>
            <el-tag v-else-if="scope.row.pay_status === '1'" type="success">
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" width="80px">
        </el-table-column>
        <el-table-column prop="create_time" label="" width="200px">
          <template slot-scope="scope">
            {{scope.row.create_time | dataFormat}}
          </template>
        </el-table-column>
        <el-table-column label="" width="150px">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" icon="el-icon-edit"
              @click="showEdit(scope.row)"></el-button>
            <el-button type="success" size="mini" icon="el-icon-location"
              @click="showLocation(scope.row)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination @size-change="pageSizeChange"
        @current-change="pagenumChange" :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]" :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="editOrderVisible" width="50%"
      :before-close="editOrderClose">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef"
        label-width="100px">
        <el-form-item label="/" prop="address">
          <el-cascader :options="cityOptions" v-model="editForm.address">
          </el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="editForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="editOrderVisible = false"> </el-button>
        <el-button type="primary" @click="submitEdit()"> 
        </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="showLocationVisible" width="50%"
      :before-close="showLocationClose">
      <el-timeline :reverse="true">
        <el-timeline-item v-for="(activity, index) in kuaidiList" :key="index"
          :timestamp="activity.time">
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import { getOrdersList, getKuaidiList } from 'network/order/order.js'

import cityOptions from 'common/city_data2017_element.js'

export default {
  name: 'Order',
  components: {},
  data () {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      total: 0,
      orderList: [],
      // 
      editOrderVisible: false,
      editForm: {
        address: '',
        address2: ''
      },
      editFormRules: {
        address: [
          { required: true, message: '', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      cityOptions: cityOptions,
      // 
      showLocationVisible: false,
      kuaidiList: []
    }
  },
  computed: {},
  // data
  watch: {},
  methods: {
    // 
    showLocation (row) {
      this.getKuaidiList()
      this.showLocationVisible = true
    },
    // 
    showLocationClose () {
      this.showLocationVisible = false
    },
    // 
    submitEdit () {
      this.$refs.editFormRef.validate(validate => {
        if (!validate) {
          return false
        }
        this.editOrderVisible = false
      })
    },
    // 
    showEdit (row) {
      this.editOrderVisible = true
    },
    // 
    editOrderClose () {
      this.editOrderVisible = false
    },
    // 
    pagenumChange (newNum) {
      this.queryInfo.pagenum = newNum
      this.getOrdersList()
    },
    // 
    pageSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrdersList()
    },
    // 
    getOrdersList () {
      getOrdersList(this.queryInfo).then(res => {
        // console.log(res)
        if (res.data.meta.status !== 200) return this.$message.error(res.data.meta.msg)
        this.total = res.data.data.total
        this.orderList = res.data.data.goods
      }).catch(err => {
        console.log(err)
      })
    },
    // 
    getKuaidiList (id = '1106975712662') {
      getKuaidiList(id).then(res => {
        if (res.data.meta.status !== 200) return this.$message.error(res.data.meta.msg)
        this.kuaidiList = [
          {
            time: '2018-05-10 09:39:00',
            ftime: '2018-05-10 09:39:00',
            context: ',,',
            location: ''
          },
          {
            time: '2018-05-10 08:23:00',
            ftime: '2018-05-10 08:23:00',
            context: '[]  95338',
            location: ''
          },
          {
            time: '2018-05-10 07:32:00',
            ftime: '2018-05-10 07:32:00',
            context: ' []',
            location: ''
          },
          {
            time: '2018-05-10 02:03:00',
            ftime: '2018-05-10 02:03:00',
            context: '[], []',
            location: ''
          },
          {
            time: '2018-05-09 23:05:00',
            ftime: '2018-05-09 23:05:00',
            context: ' []',
            location: ''
          },
          {
            time: '2018-05-09 21:21:00',
            ftime: '2018-05-09 21:21:00',
            context: '[], []',
            location: ''
          },
          {
            time: '2018-05-09 13:07:00',
            ftime: '2018-05-09 13:07:00',
            context: ' ',
            location: ''
          },
          {
            time: '2018-05-09 12:25:03',
            ftime: '2018-05-09 12:25:03',
            context: '',
            location: ''
          },
          {
            time: '2018-05-09 12:22:24',
            ftime: '2018-05-09 12:22:24',
            context: 'HLA',
            location: ''
          },
          {
            time: '2018-05-08 21:36:04',
            ftime: '2018-05-08 21:36:04',
            context: '',
            location: ''
          }
        ]
      }).catch(err => {
        console.log(err)
      })
    }
  },
  //  - this
  created () {
    this.getOrdersList()
  },
  //  - DOM
  mounted () {

  },
  beforeCreate () { }, //  - 
  beforeMount () { }, //  - 
  beforeUpdate () { }, //  - 
  updated () { }, //  - 
  beforeDestroy () { }, //  - 
  destroyed () { }, //  - 
  activated () { } // keep-alive
}
</script>

<style scoped>
.el-cascader {
  width: 100%;
}
</style>
